/*Caixa de seleção*/
#container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: #FFFF;
    max-width: 80%;
    margin-left: auto;
    margin-right: auto;
    opacity: 80%;
}

            #rec {
                display: flex;
                background-color: #329F64;
                width: 500px;
                height: 350px;
                border: 10px;
                align-items: center;
                justify-content: center;
                font-size: 90px;
                text-align: center;
                font-family: 'Gurajada';
                font-style: normal;
            }

            #est {
                width: 500px;
                height: 350px;
                background: rgba(47, 147, 129, 0.8);
                border: 10px;
                font-size: 90px;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                font-family: 'Gurajada';
                font-style: normal;
            }

            #rec:hover {
                background-color: #802828;
                transition: 0.5s;
            }

            #est:hover {
                background-color: #802828;
                transition: 0.5s;
            }


            

#container3{
display: block;
